@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}


nav {
  height: 50px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 25px;
}

.logo {
  background-color: #d0bfff;
  width: 100px;
  text-align: center;
  margin-top: 15px;
  border-radius: 12px;
  font-size: 35px;
  position: absolute;
  left: 12px;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
    7px 7px 20px 0px rgba(0, 0, 0, .1),
    4px 4px 5px 0px rgba(0, 0, 0, .1);
}

.logo a {
  font-family: 'Times New Roman', Times, serif;
  text-decoration: none;
  color: #111;
}

.card {
  margin: auto;
  width: 140px;
  height: 100px;
  border-radius: 12px;
  background-color: #b4e4ff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
    7px 7px 20px 0px rgba(0, 0, 0, .1),
    4px 4px 5px 0px rgba(0, 0, 0, .1);
}

.card button {
  margin-top: 3px;
  height: 20px;
  width: 60px;
  border: 0;
  border-radius: 12px;
  background-color: #fdcedf;
  cursor: pointer;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
    4px 4px 10px 0px rgba(0, 0, 0, .1),
    2px 2px 3px 0px rgba(0, 0, 0, .1);
}

.dailyTransaction {
  display: flex;
  text-align: center;
  width: 95%;
  /* background-color: yellow; */
  margin: 20px auto;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 80px;
}

.dailyTransaction .row {
  display: flex;
}

.dailyTransaction .head {
  font-weight: bold;
}


.dailyTransaction .row div {
  border: 1px solid #111;
  padding: 4px;
}

.dailyTransaction .srno {
  /* background-color: red; */
  width: 60px;
}

.dailyTransaction .date {
  /* background-color: blue; */
  width: 140px;
}

.dailyTransaction .name {
  /* background-color: #fdcedf; */
  width: 200px;
}

.dailyTransaction .amount {
  /* background-color: aqua; */
  width: 100px;
}

.dailyTransaction .deletehead {
  border: 0 !important;
  width: 30px;
}

.dailyTransaction .delete {
  width: 30px;
  margin: auto;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dailyTransaction .delete:hover {
  color: #fc2947;
}

.addTransaction {
  position: fixed;
  bottom: 10%;
  /* background-color: red; */
  width: 100%;
  display: flex;
  justify-content: center;
}

.addTransaction input {
  margin: auto;
  margin: 5px;
}

.addTransaction button {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 0;
  background-color: #beadfa;
  font-size: 22px;
}

.edit {
  position: fixed;
  right: 20px;
  top: 20px;
}

.confirmDelete {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  background-color: #fafafa73;
}

.confirmDelete .box {
  height: 100px;
  width: 300px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #beadfa;
}

.confirmDelete .box button {
  height: 30px;
  width: 60px;
  margin: 5px;
  background-color: #f8f8f8;
  border: 0;
  border-radius: 12px;
  cursor: pointer;
}

.main h1 {
  text-align: center;
}